.unitNaming {
  overflow: auto;
  &::-webkit-scrollbar {
    width: 0.3em;
    height: 4px;
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgb(255, 255, 255);
    outline: 0.2px solid rgba(183, 186, 189, 0.331);
    border-radius: 5%;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgb(62, 62, 62)3;
    outline: 0.1px solid rgba(183, 186, 189, 0.257);
    border-radius: 5%;
  }
  .tab_design {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 60px;
    align-items: center;
    // border-bottom: 1px solid #bdbdbd;
  }

  .unit_tab_design_active {
    font-size: 16px;
    font-weight: 600;
    margin-right: 50px;
    cursor: pointer;
    height: 60px;
    display: flex;
    align-items: center;
    color: #367bbf;
    border-bottom: 5px solid #367bbf;
  }

  .unit_tab_design_inActive {
    font-size: 16px;
    font-weight: 400;
    cursor: pointer;
    height: 60px;
    display: flex;
    align-items: center;
    margin-right: 50px;
    color: #4c4c4c;
  }

  .unit_room_package_tab {
    display: flex;
    padding: 4px 6px;
    background-color: #fff;
    border-bottom: none;
    height: auto;

    
    // border-radius: 4px;
    .unit_tab_design_inActive {
      padding: 2px 25.5px;
      font-size: 16px;
      font-weight: 600;
      line-height: 24px;
      color: #787c7e;
      height: auto;
      margin-right: 0;
      font-family: Barlow;
    }

    .unit_tab_design_active {
      //   border-bottom: none;
      padding: 2px 21.5px;
      background-color: #fff;
      margin-right: 0;
      height: auto;
      font-weight: 600;
      //   font-size: 14px;
      //   line-height: 20px;
      //   font-family: "Inter";
      color: #1e2528;
      position: relative;
      //   border-radius: 4px;
      // border-top: 10px solid red;
      // border-top-right-radius: 10px;
      // border-bottom: 10px solid red;
      // // border-bottom-right-radius: 10px;
      // border-bottom-color: #8248cb;
      // border-radius: 5rem;
      // bottom: -4px;
      // height: 4px;
      font-family: Barlow;
      font-size: 16px;
      font-style: normal;

      line-height: 24px;

      //   background-color: #8248cb;
      //   color: #fff; /* You can adjust the text color as needed */
    }

    .unit_tab_design_active::before {
      content: "";
      position: absolute;
      bottom: -3px;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: #8248cb;
      border-radius: 3px 3px 3px 3px;
      /* Rounded corners for the bottom line */
    }
  }
}
